import { H1, H2, H3, H4, H5, H6 } from '@ali/4ever-plugin-heading';
import { P } from '@ali/4ever-plugin-paragraph';
import { TD } from '@ali/4ever-component';

const typeToComponent = {
  'table-cell': TD,
  'heading-1': H1,
  'heading-2': H2,
  'heading-3': H3,
  'heading-4': H4,
  'heading-5': H5,
  'heading-6': H6,
  paragraph: P,
};

type TypeToComponent = typeof typeToComponent;
export type TypeToComponentKeys = keyof TypeToComponent;

export function styledComponents<K extends keyof TypeToComponent>(type: K): TypeToComponent[K] {
  return typeToComponent[type];
}